.LoginPageButton {
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: #202124;
    
    color: #5E6472;
}

.LoginPageButton:hover {
    -webkit-transform: translatey(1px);
            transform: translatey(1px);
    box-shadow: none;
}

.LoginPageButton:hover {
-webkit-animation: ani 0.6s ease-in-out infinite alternate;
        animation: ani 0.6s ease-in-out infinite alternate;
}
@-webkit-keyframes ani {
    0% {
        -webkit-transform: translateY(1px);
                transform: translateY(1px);
    }
    100% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
}
@keyframes ani {
    0% {
        -webkit-transform: translateY(1px);
                transform: translateY(1px);
    }
    100% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
}

.LoginPageButton:disabled {
    -webkit-animation: none;
            animation: none;
    background-color: #202124;
}

.LoginPageButton:hover:disabled {
    -webkit-animation: none;
            animation: none;
    -webkit-transform: none;
            transform: none;
}

